import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CategoryProductsService } from '../../services/category-products.service';

@Component({
  selector: 'app-category-slider',
  templateUrl: './category-slider.component.html',
  styleUrls: ['../components.scss'],
})
export class CategorySliderComponent implements OnInit {

  constructor(private router: Router, private categoryService: CategoryProductsService) { }

  Categories;

  categorySlideOpts = {
    slidesPerView: 2,
  }


  ngOnInit() {
    this.getCategories();
  }

  getCategories() {
    this.categoryService.getCategories()
      .subscribe(data => {
        this.Categories = data;
      })
  }


  selectCategory(c) {
    let data = {
      queryParams: {
        categoryId: c.CategoryId,
        categoryName: c.CategoryName
      }
    }

    this.router.navigate(['/category-view'], data);
  }

  browseAll(id) {
    let data = {
      queryParams: {
        browseId: id
      }
    }
    this.router.navigate(['/browse-by-all'], data);
  }
}
